  // *** Merge Auth and DB User API *** //
  onAuthUserListener = (next, fallback) => 

    this.auth.onAuthStateChanged(authUser => {
    console.log('o.log > firebase - authuser listener')
    //console.log('o.log > authuser outside x = ' +email) // fail
    //console.log('o.log > authuser outside x = ' +authUser) // fail = undefined ?
    //console.log('o.log > authuser outside x = ' +authUser.email) // fail = null is not an object
      if (authUser) {
        this.user(authUser.uid)
          .once('value')
          .then(snapshot => {

            console.log('o.log > authuser inside = ' +authUser.email)
            const dbUser = snapshot.val();

            // default empty roles
            if (!dbUser.roles) {
              dbUser.roles = {};
            }

            // merge auth and db user
            authUser = {
              uid: authUser.uid,
              email: authUser.email,
              emailVerified: authUser.emailVerified,
              providerData: authUser.providerData,
              ...dbUser,
            };

            next(authUser);
          });
      } else {
        fallback();
      }
    });


  // *** User API ***
  user = uid => this.db.ref(`users/${uid}`);
  users = () => this.db.ref('users');

  // *** Message API ***
  // extend the interface for your Firebase class for the user entity. It defines two new functions:
  message = uid => this.db.ref(`messages/${uid}`); // one to get a reference to a user by identifier (uid) 
