// this is the PARENT ; all others are Childs
import React from 'react';
//import { BrowserRouter as Router, Route } from 'react-router-dom'; // old
import { Router, Route, Link } from "../App/router-web"; // new route mechanism


import Navigation from '../Navigation';
import LandingPage from '../Landing';
import SignupPage from '../Signup'; // og.fixme: leave out for public repo
import LoginPage from '../Login';
import LogoutPage from '../Logout';
//import PasswordForgetPage from '../PasswordForget'; // og.fixme: leave out for public repo
import HomePage from '../Home';
import AboutPage from '../About'; // test
//import AccountPage from '../Account'; // og.fixme: leave out for public repo
//import AdminPage from '../Admin'; // og.fixme: leave out for public repo

import * as ROUTES from '../../common/routes';
import { withAuthenticationLs } from '../../common/Session'; // uses LS aka localStorage

// author > If a route matches a path prop, the respective component will be displayed.
// 
// it uses the higher-order component to make the authenticated user available 
// for all other components below of the App component:
const LayoutContainer = () => (
  <Router>
    <div className="container">
      <Navigation />
      <hr />
      <Route exact path={ROUTES.LANDING} component={LandingPage} />
      <Route path={ROUTES.LOGIN} component={LoginPage} />
      <Route path={ROUTES.HOME} component={HomePage} />
      <Route path={ROUTES.ABOUT} component={AboutPage} />
      <Route path={ROUTES.LOGOUT} component={LogoutPage} />
      <Route path={ROUTES.SIGNUP} component={SignupPage} />
    </div>
  </Router>
);

export default withAuthenticationLs(LayoutContainer); // ok


