import React, { Component } from 'react';
//import { withRouter } from 'react-router-dom';
import { Link, withRouter } from 'react-router-dom';
import { compose } from 'recompose'; // og.note: replaced by React Hooks

//import { SignupLink } from '../Signup'; // merged / right after loginform
//    <SignupLink />

//import { PasswordForgetLink } from '../PasswordForget';

import { withFirebase } from '../../common/Firebase';
import * as ROUTES from '../../common/routes';
import * as CONSTANTS from '../../common/constants';

// Login/Logout = App ; SignIn/SignOut = Firebase

// og.note * using HTML5 form validation
//         * if refactor, we need disable HTMl5 form validation
//           see https://codeburst.io/how-to-use-html5-form-validations-with-react-4052eda9a1d4

// og.note * form input length needs to be px in order for consistent UI
//         * HTML5 validation only works when button type is submit
//           per https://stackoverflow.com/questions/16707743/html5-validation-when-the-input-type-is-not-submit
const LoginPage = () => (
  <div className="container">
    <h2>{CONSTANTS.TEXT_PLEASE}</h2>
    <LoginForm />
  </div>
);

const INITIAL_STATE = {
  email: '',
  password: '',
  error: null,
};

// const ERROR_CODE_ACCOUNT_EXISTS = 'account-exists-with-different-credential';
const ERROR_CODE_ACCOUNT_EXISTS = `account-exists-with-different-credential`;

const ERROR_MSG_ACCOUNT_EXISTS = `An account already exists.`;

class LoginFormBase extends Component {
  constructor(props) {
    super(props);

    this.state = { ...INITIAL_STATE };
  }

  onSubmit = event => {
    const { email, password } = this.state;

    this.props.firebase
      .doSignInWithEmailAndPassword(email, password) // og.fixme ? signIn
      .then(() => {
        this.setState({ ...INITIAL_STATE });
        this.props.history.push(ROUTES.HOME);
      })
      .catch(error => {
        this.setState({ error });
      });

    event.preventDefault();
  };

  onChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  render() {
    const { email, password, error } = this.state;

    const isInvalid = password === '' || email === '';

    return (
      <form className="formContainer" onSubmit={this.onSubmit}>

        <input
          name="email"
          required
          value={email}
          onChange={this.onChange}
          type="text"
          placeholder="Email Address"
          pattern=".*\S.*"
        />
        
        <input
          name="password"
          required
          value={password}
          onChange={this.onChange}
          type="password"
          placeholder="Password"
          pattern=".*\S.*"
        />

        <button className="formButton formButtonText" type="submit">
          {CONSTANTS.TEXT_LOGIN}
        </button>

        <p className='textCenter'>{CONSTANTS.UX_SIGNUP_WEB}<Link to={ROUTES.SIGNUP}>{CONSTANTS.TEXT_SIGNUP}</Link></p>

        <span className='textRed'>{error && <p>{error.message}</p>}</span>
      </form>
    );
  }
}

const LoginForm = compose(
  withRouter,
  withFirebase,
)(LoginFormBase);

export default LoginPage;

export { LoginForm };

