import React from 'react';

import AuthUserContext from './context';
import { withFirebase } from '../Firebase';

import * as MOBILE from '../mobile.js'; // used to provide localStorage to RN
//import * as MOBILE from '../../common/mobile.js'; // used to provide localStorage to RN
import AsyncStorage from '@react-native-community/async-storage'; // used to provide localStorage to RN

console.log('o.log > app/common/Session/withAuthenticationAs.js called')

// The application works the same as before, except any component can simply use React’s Context 
// to consume the authenticated user. To keep the App component clean and concise, I like to 
// extract the session handling for the authenticated user to a separate higher-order component 
// in a new src/components/Session/withAuthentication.js file
// 
// ref https://www.robinwieruch.de/complete-firebase-authentication-react-tutorial/

// withAuthentication = wrapper
// WithAuthentication = firebase
const withAuthenticationAs = Component => {
  class WithAuthen extends React.Component {
    constructor(props) {
      super(props);

      this.state = {
        //authUser: JSON.parse(localStorage.getItem('authUser')),
        //authUser: JSON.parse(MOBILE.lsGet('authUser')), // Custom RN method
        authUser: MOBILE.lsGet('authUser'), // Custom RN method
      };
    }

    componentDidMount() {
      console.log('o.log > withAuthenticationAs - component DID mount')
      // see below re listener
      this.listener = this.props.firebase.onAuthUserListener(
        authUser => {
          //localStorage.setItem('authUser', JSON.stringify(authUser)); // creates problem for react-native
          MOBILE.lsStore('authUser', JSON.stringify(authUser)); // Custom RN method
          this.setState({ authUser });
        },
        () => {
          //localStorage.removeItem('authUser'); // creates problem for react-native
          const keys = ['authUser'] 
          MOBILE.lsClear(keys); // Custom RN method
          this.setState({ authUser: null });
        },
      );
    }

    // og.fixme = Warning: componentWillMount is deprecated and will be removed in the next major version. 
    // Use componentDidMount instead. As a temporary workaround, you can rename to UNSAFE_componentWillMount.
    componentWillUnmount() {
      console.log('o.log > withAuthenticationAs - component WILL mount')
      this.listener(); // og.fixme: believe this is waiting for click ?
      
    }

    render() {
      return (
        <AuthUserContext.Provider value={this.state.authUser}>
          <Component {...this.props} />
        </AuthUserContext.Provider>
      );
    }
  }

  return withFirebase(WithAuthen);
};

export default withAuthenticationAs;
