import { Link, withRouter } from 'react-router-dom';
import { compose } from 'recompose';

// author > You can use recompose to organize your higher-order components. 
// Since the higher-order components don’t depend on each other, the order 
// doesn’t matter. Otherwise, it may be good to know that the compose 
// function applies the higher-order components from right to left.

import { withFirebase } from '../../common/Firebase';
import * as ROUTES from '../../common/routes';
import * as ROLES from '../../common/roles';
import * as CONSTANTS from '../../common/constants';

console.log('o.log > app/components/Signup/SignupView.js called')

// o.note * using custom css ; may use Burma or Bootstarp ()
