// og.note * using HTML5 form validation
//         * if refactor, we need disable HTMl5 form validation
//           see https://codeburst.io/how-to-use-html5-form-validations-with-react-4052eda9a1d4

// og.note * form input length needs to be px in order for consistent UI
//         * HTML5 validation only works when button type is submit
//           per https://stackoverflow.com/questions/16707743/html5-validation-when-the-input-type-is-not-submit    
const SignupPage = () => (
  <div className='container'>
    <h2>Signup</h2>
    <SignupForm />
  </div>
);

const INITIAL_STATE = {
  username: '',
  email: '',
  passwordOne: '',
  passwordTwo: '',
  isAdmin: false,
  error: null,
};

const ERROR_CODE_ACCOUNT_EXISTS = "auth/email-already-in-use";
const ERROR_MSG_ACCOUNT_EXISTS = 'Account already exists.';

class SignupFormBase extends Component {
  constructor(props) {
    super(props);

    this.state = { ...INITIAL_STATE };
  }

  onSubmit = event => {
    const { username, email, passwordOne, isAdmin } = this.state;
    const roles = {};

    if (isAdmin) {
      roles[ROLES.ADMIN] = ROLES.ADMIN;
    }

    this.props.firebase
      .doCreateUserWithEmailAndPassword(email, passwordOne)
      .then(authUser => {
        // Create a user in your Firebase realtime database
        return this.props.firebase.user(authUser.user.uid).set({
          username,
          email,
          roles,
        });
      })
      .then(() => {
        return this.props.firebase.doSendEmailVerification();
      })
      .then(() => {
        this.setState({ ...INITIAL_STATE });
        this.props.history.push(ROUTES.HOME);
      })
      .catch(error => {
        if (error.code === ERROR_CODE_ACCOUNT_EXISTS) {
          error.message = ERROR_MSG_ACCOUNT_EXISTS;
        }

        this.setState({ error });
      });

    event.preventDefault();
  };

  onChange = event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  onChangeCheckbox = event => {
    this.setState({ [event.target.name]: event.target.checked });
  };


//        <button className='buttonStyle textStyle' disabled={isInvalid} type='submit'>
//        not sure why button disabled per https://www.w3schools.com/tags/att_button_disabled.asp
  render() {
    const {
      username,
      email,
      passwordOne,
      passwordTwo,
      isAdmin,
      error,
    } = this.state;

    // og.note: * error checking is form order. 
    //          * believe password checking does not work reliably via !== should be !=== or the Firebase SDK does not work reliably
    const isInvalid =
      passwordOne !== passwordTwo ||
      passwordOne === '' ||
      email === '' ||
      username === '';

    return (
      <form className="formContainer" onSubmit={this.onSubmit}>

        <input
          name="username"
          required
          value={username}
          onChange={this.onChange}
          type="text"
          placeholder="Full Name"
          pattern=".*\S.*"
          size='32'
         />

        <input
          name="email"
          required
          value={email}
          onChange={this.onChange}
          type="email"
          placeholder="Email Address"
          pattern=".*\S.*"
         />

        <input
          name="passwordOne"
          required
          value={passwordOne}
          onChange={this.onChange}
          type="password"
          placeholder="Password"
          pattern=".*\S.*"
         />

        <input
          name="passwordTwo"
          required
          value={passwordTwo}
          onChange={this.onChange}
          type="password"
          placeholder="Confirm Password"
          pattern=".*\S.*"
         />

        <button className="formButton formButtonText" type="submit">
          {CONSTANTS.TEXT_SIGNUP}
        </button>

        <p>Already have an account, <Link to={ROUTES.LOGIN}>Login</Link></p>

         <span className='textRed'>{error && <p>{error.message}</p>}</span>
      </form>
    );
  }
}

const SignupForm = compose(
  withRouter,
  withFirebase,
)(SignupFormBase);

export default SignupPage;

export { SignupForm };
