export const lsGet = async (aKey) => {
//export const lsGet = async (aKey) => {
  try {
    const value = await AsyncStorage.getItem(aKey) || 'none';
    if (value !== null) {
      // We have data!!
      //console.log('get key = ' +aKey)
      //console.log(value);
      //return value
      return JSON.parse(value)
    }
  } catch (error) {
      // Error retrieving data
      console.log('LS error - can not read \n' +error)
      return null
  }
};

