// author > You have all the components needed to fulfil an authentication roundtrip in React, 
// and all that’s missing is an overseer for the session state. Logic regarding the current 
// authenticated user needs to be stored and made accessible to other components. This is 
// often the point where developers start to use a state management library like Redux or MobX. 
// Without these, we’ll make due using global state instead of state management libraries.
// 
// Since our application is made under the umbrella of App component, it’s sufficient to manage 
// the session state in the App component using React’s local state. The App component only 
// needs to keep track of an authenticated user (session). If a user is authenticated, store 
// it in the local state and pass the authenticated user object down to all components that 
// are interested in it. 
// 
// Otherwise, pass the authenticated user down as null. That way, all components interested in 
// it can adjust their behavior (e.g. use conditional rendering) based on the session state. 
// For instance, the Navigation component is interested because it has to show different 
// options to authenticated and non authenticated users. The SignOut component shouldn’t 
// show up for a non authenticated user, for example.

export {
  AuthUserContext,
  withAuthenticationAs,
  withAuthenticationLs,
  withAuthorization,
  withEmailVerification,
};
