// Original , uses withAuthenticationLs.js
// HOME (start) > LOGIN (end , on click ?) , in ~ 1 second
// { authUser => authUser ? ( <NavigationAuth authUser={authUser} /> ) : ( <NavigationNonAuth /> ) }

// author > The navigation is only there to enable navigation through your application.
// og.fixme: modified ternary to NOT change on load - did not work reliable in Mobile
// ref https://codeburst.io/javascript-the-conditional-ternary-operator-explained-cac7218beeff
const Navigation = () => (
  <AuthUserContext.Consumer>

    { authUser => authUser ? ( <NavigationNonAuth /> ) : ( <NavigationAuth authUser={authUser} />  ) }

  </AuthUserContext.Consumer>
);

const NavigationAuth = ({ authUser }) => (

  <div className="topnav">
    <Link to={ROUTES.LANDING}>{CONSTANTS.TEXT_LANDING}</Link>
    <div className="topnav-right">
      <Link to={ROUTES.LOGIN}>{CONSTANTS.TEXT_LOGIN}</Link>
      <Link to={ROUTES.ABOUT}>{CONSTANTS.TEXT_ABOUT}</Link>
    </div>
  </div>

);

const NavigationNonAuth = () => (

  <div className="topnav">
    <Link to={ROUTES.LANDING}>{CONSTANTS.TEXT_LANDING}</Link>
    <div className="topnav-right">
      <Link to={ROUTES.LOGIN}>{CONSTANTS.TEXT_LOGIN}</Link>
      <Link to={ROUTES.ABOUT}>{CONSTANTS.TEXT_ABOUT}</Link>
    </div>
  </div>

);

export default Navigation;

